import styled from 'styled-components';

const styledSection = styled.div`
  padding: 2rem;
`;

const Section = props => {
  return (
    <StyledSection>
      {props.container ? (
        <h1>true</h1>
      ) : (
        <h1>false</h1>
      )}
      {this.props.children}
    </StyledSection>
  );
}

export default Section;
